/* Mobile first approach */

.mainContainer {
  max-width: 1024px;
  margin: 0 auto;
}

.productsBody {
  min-height: 100vh;
  display: grid;
  grid-template:
    "header header"
    "main main"
    "footer footer";
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
}

header {
  grid-area: header;
  padding: 1rem 0;
  box-shadow: 0px 10px 30px rgb(0 0 0 / 12%);
  margin-bottom: 2px;
}

aside {
  grid-area: aside;
  display: none;
  background-color: #ebecec;
}

aside div {
  text-align: left;
  border-bottom: 1px solid grey;
  padding: 10px;
}

main {
  grid-area: main;
}

footer {
  grid-area: footer;
  background-color: #ebecec;
}

.container {
  display: flex;
  align-items: flex-end;
  max-width: 1024px;
  margin: 0 auto;
}

.container img {
  height: 80px;
}

.container > nav {
  /* flex-grow: 1; */
  flex: 1;
}

.container > section {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.container > section > button {
  display: flex;
  align-items: center;
  margin-top: 1rem;
}

.container > section > button svg {
  height: 24px;
  width: 24px;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  gap: 0.5rem;
}

ul > li > a {
  padding: 0.5rem;
}

a {
  text-decoration: none;
  color: initial;
}

section.products {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  background-color: #ebecec;
}

section.products > div.products__item {
  padding: 0.75rem;
  display: grid;
  grid-template:
    "title title" 30px
    "image content" 1fr
    "image buyNow" 46px / 1fr 1fr;
  align-items: center;
  border-bottom: 1px dashed #bdc2c2;
  background-color: white;
}

section.products > div.products__item > span {
  display: none;
}

section.products > div.products__item > h2 {
  grid-area: title;
}

section.products > div.products__item > img {
  grid-area: image;
  width: 100%;
}

section.products > div.products__item > p {
  grid-area: content;
  background-color: #ebecec;
}

section.products > div.products__item > span {
  grid-area: price;
}

section.products > div.products__item > button {
  grid-area: buyNow;
  height: 46px;
  border: none;
  background-color: #a81444;
  color: #fff;
}

@media screen and (min-width: 768px) {
  .productsBody {
    grid-template:
      "header header"
      "aside main" 1fr
      "footer footer" 50px / 20% 1fr;
  }

  aside {
    display: block;
  }

  section.products > div.products__item {
    grid-template:
      "title title"
      "image content"
      "buyNow buyNow";
  }
}

@media screen and (min-width: 1024px) {
  section.products > div.products__item {
    grid-template:
      "title title"
      "image image"
      "content content"
      "price buyNow";
  }

  section.products > div.products__item > span {
    display: block;
  }
}

/* Home styles */

.homeBody {
  min-height: 100vh;
  display: grid;
  grid-template:
    "header header"
    "main main"
    "footer footer";
}

.btn {
  height: 46px;
  border: none;
  background-color: #a81444;
  color: #fff;
}

.btn--full {
  width: 100%;
}

.categories {
  margin: 10% 0%;
}

.categories__item {
  display: flex;
  height: 300px;
  background-color: white;
  position: relative;
  margin: 5% 0%;
}

.categories__item:after {
  width: 80%;
  height: 100px;
  box-shadow: 0px 0px 40px rgb(0 0 0 / 18%);
  border-radius: 100%;
  position: absolute;
  bottom: 0;
  content: "";
  z-index: -1;
  left: 0;
  right: 0;
  margin: 0 auto;
}

.categories__item > div {
  display: flex;
  flex-direction: column;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.categories__item > div > img {
  width: 90%;
}

.categories__item > div > h1,
.categories__item > div > p {
  text-align: center;
}

.carousal {
  display: flex;
  height: 300px;
  justify-content: center;
  box-shadow: 0 8px 8px -12px;
}

.carousal > img {
  height: 100%;
}

.categories__item:nth-child(even) {
  flex-direction: row-reverse;
}

/* LOGIN PAGE */

.loginMain {
  display: flex;
  margin-top: 2rem !important;
  margin-bottom: 2rem !important;
  align-items: center;
}

main > div,
main > form {
  flex: 1;
}

/* animated button */

form {
  display: block;
}

.form-field {
  width: 100%;
  position: relative;
  overflow: hidden;
  margin: 2rem 0;
  text-align: left;
}

.form-field > .form-field__label {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding: 1.5rem 0 0;
  font-size: 0.75rem;
  transform: translateY(-1rem);
  transition: all 0.4s;
}

.form-field > .form-field__bar {
  border-bottom: 2px solid dodgerblue;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  z-index: 1;
  width: 1%;
  margin: 0 auto;
  display: block;
  transform: scaleX(0);
  transition: all 0.4s;
}

.form-field > .form-field__input {
  appearance: none;
  background: transparent;
  border: 0;
  font-size: 1.2rem;
  outline: 0;
  width: 100%;
  padding: 0 0.75rem 0.625rem 0;
  border-bottom: 1px solid #999;
  margin-top: 1.5rem;
}

.form-field > .form-field__input:-ms-input-placeholder ~ .form-field__label {
  font-size: 1.2rem;
  transform: translateY(0);
}

.form-field > .form-field__input:placeholder-shown ~ .form-field__label {
  font-size: 1.2rem;
  transform: translateY(0);
}

.form-field > .form-field__input:focus + .form-field__label {
  font-size: 0.75rem;
  transform: translateY(-1rem);
}

.form-field > .form-field__input:focus ~ .form-field__bar {
  transform: scaleX(150);
}
